body {
  display: flex;
  flex-direction: column;
  background-color: #fcf5da;
}
body * {
  display: flex;
  flex-direction: column;
}

* {
  margin: 0;
  padding: 0;
}

h1 {
  font-size: 10vw;
  padding: 10px 20px;
}

h2 {
  font-size: 5vw;
  padding: 0 10px 10px 20px;
  border-bottom: 1px solid black;
}

a {
  color: inherit;
  text-decoration: none;
}
a:hover {
  font-weight: 600;
}

html {
  overflow-y: scroll;
  scrollbar-width: none; /* Firefox */
  -ms-overflow-style: none; /* Internet Explorer 10+ */
}
html::-webkit-scrollbar {
  width: 0;
  height: 0;
}

h6 {
  font-size: 24px;
  padding: 10px 20px;
  font-weight: 500;
}

p {
  font-size: 24px;
  display: inline;
}

a {
  display: inline;
}

.block-full {
  width: 90%;
  padding: 20px;
  margin: 20px;
}

.border {
  border: 2px solid black;
}

