
.front {
  width: 100vw;
  height: 100vh;

  align-items: center;
  justify-content: center;
}

.intro-text {
  border-left: 2px solid black;
}

.flex-width {
  width: 80%;
  display: flex;
}

.flex-height {
  height: 80%;
  display: flex;
  flex-direction: column;
}

.header {
  position: fixed;
  display: flex;
  flex-direction: row;

  left: 20px;
  top: 20px;
  border: 2px solid black;

  a {
    img{
      height: 32px;
      width: 32px;
      margin: 10px 15px;
    }
  }
}
